html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-figtree);
  background-color: var(--yellow);
  color: var(--black);
  min-height: 100vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --yellow_: hsl(47, 88%, 63%);
  --white_: hsl(0, 0%, 100%);
  --grey_: hsl(0, 0%, 50%);
  --black_: hsl(0, 0%, 7%);
}

:root {
  --font-figtree:"Figtree", sans-serif;
}

.card {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--yellow_);
}

.card_content {
  width: 23.75rem;
  height: 31.25rem;
  border-radius: 1.25rem;
  border: 0.0625rem solid var(--black_);
  background-color: var(--white_);
  filter: drop-shadow(0.5rem 0.5rem var(--black_));
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 40em) {
  .card_content {
    width: 25rem;
    height: 32.5rem;
  }
}

.card_img {
  height: 12.5rem;
  background-size: cover;
  border-radius: 0.625rem;
  overflow: hidden;
  margin-top: 0.9375rem;
}

.smallText {
  width: 20.3125rem;
  margin-top: 0.9375rem;
}

.card_tag {
  width: 5.4375rem;
  font-weight: 800;
  height: 1.875rem;
  background-color: var(--yellow_);
  padding: 0.625rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.625rem;
}

.card_date {
  width: 20.3125rem;
  height: 1.875rem;
  margin-top: 0.875rem;
}

.card_heading {
  width: 20.3125rem;
  font-size: 1.5625rem;
  font-weight: 800;
}
.card_heading:hover {
  color: var(--yellow_);
  cursor: pointer;
}

.card_desc {
  width: 20.3125rem;
  color: var(--grey_);
  font-size: 1rem;
  line-height: 1.5;
}

.card_author {
  margin-top: 0.625rem;
  margin-bottom: 0.625rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 4.0625rem;
}

.card_authorImg {
  padding-right: 0.625rem;
}
.card_authorImg img {
  height: 2.5rem;
}

.card_authorDesc {
  width: 17.4375rem;
  font-weight: 800;
  font-size: 0.9375rem;
}